export default {
	props: {
		selPrivince: {
			type: Array,
			default: []
		},
		selCityInfo: {
			type: Array,
			default: []
		},
		selCounty: {
			type: Array,
			default: []
		},
		isShowCity: {
			type: Boolean,
			default: false
		},
		cityType: {
			type: Number,
			default: 0
		},
		moduleType: {
			type: Number,
			default: 1
		},
		selCityNameS: {}
	},
	data() {
		return {
		}
	},
	onLoad() {
		
	},
	methods: {
		selCity (type) {
			this.$emit('selCity', type);
		},
		closeAlert () {
			this.$emit('closeAlertCity');
		},
		okCityValue (item, index) {
			item.index = index;
			this.$emit('okCityValue',  item);
		},
		closeAdd () {
			this.$emit('saveCustomer');
		},
		saveCustomer () {
			this.$emit('saveCustomer');
		},
		goPlaceOrder () {
			this.$API.to(`../../sale/invoice/invoice?type={this.moduleType}`);
		}
	}
	
}
